<template>
  <router-view/>
</template>

<script lang="ts" setup>
import { onBeforeMount, onMounted } from 'vue'
import { useCookies } from 'vue3-cookies'
import { loadSupportLocales, setI18nLanguage } from '@/plugins/i18n'

const { cookies } = useCookies();

onBeforeMount(async () => {
  await loadSupportLocales();
});

onMounted(async () => {
  // 預設語系
  setI18nLanguage(cookies.get(process.env.VUE_APP_LOCALE_COOKIE_NAME!));
});
</script>

