import { createStore } from 'vuex'
import { setI18nLanguage } from '@/plugins/i18n'

const store = createStore({
  state: {
    /** 目前套用的語系 */
    currentCulture: process.env.VUE_APP_DEFAULT_LOCALE!
  },
  mutations: {
    /** 切換語系 */
    setCurrentCulture(state, payload) {
      state.currentCulture = payload || process.env.VUE_APP_DEFAULT_LOCALE!;
      setI18nLanguage(state.currentCulture);
    }
  }
});

export default store;
