import { nextTick } from 'vue'
import { createI18n } from 'vue-i18n'
import { useCookies } from 'vue3-cookies'

const { cookies } = useCookies();

const i18n = createI18n({
  locale: process.env.VUE_APP_DEFAULT_LOCALE,
  fallbackLocale: process.env.VUE_APP_DEFAULT_LOCALE
});

export function setI18nLanguage(locale?: string) {
  i18n.global.locale = locale || process.env.VUE_APP_DEFAULT_LOCALE!;
  /**
   * NOTE:
   * If you need to specify the language setting for headers, such as the `fetch` API, set it here.
   * The following is an example for axios.
   *
   * axios.defaults.headers.common['Accept-Language'] = locale
   */
  cookies.set(process.env.VUE_APP_LOCALE_COOKIE_NAME!, i18n.global.locale);
  document.documentElement.setAttribute('lang', i18n.global.locale);
}

export async function loadLocaleMessages(locale: string) {
  // load locale messages with dynamic import
  const messages = await import(`@/locales/${locale}.json`);

  // set locale and locale message
  i18n.global.setLocaleMessage(locale, messages.default);

  return nextTick();
}

export async function loadSupportLocales() {
  const supportLocales: string[] = process.env.VUE_APP_SUPPORT_LOCALES!.split(',').map((x: string) => x.trim());
  for (let locale of supportLocales) {
    await loadLocaleMessages(locale);
  }
}

export default i18n;
